.rightbarFriend{
  display: flex;
  align-items: center;
  margin-bottom: 15px;

}

.rightbarProfileImgContainer{
  margin-right: 10px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  
}
.rightbarProfileImg{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 4px;
}
.rightbarOnline{
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: greenyellow;
  position: absolute;
  top: -2px;
  left: 29px;
  border: 2px solid white;
}
.rightbarUsername{
  font-weight: 500;

}
