.login{
  width: 100vw;
  height: 100vh;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;

}
.loginWrapper{
  width: 70%;
  height: 70%;
  display: flex;
}

.loginLeft, .loginRight{
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.loginLogo{
  font-size: 50px;
  font-weight: 800;
  color: #1775ee;
  margin-bottom: 10px;
}
.loginDesc{
  font-size: 24px;
}
.loginBox{
  height: 450px;
  padding: 20px;
  background-color: white;
  -webkit-box-shadow:0px 0px 16px -8px rgba(0,0,0,0.68);
  box-shadow: 0px 0px 16px -8px rgba(0,0,0,0.68);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 20px;
}
.loginInput{
  height: 50px;
  border-radius: 10px;
  border: 1px solid gray;
  font-size: 18px;
  padding-left: 20px;
}
.loginInput:focus{
  outline: none;
}
.loginBtn{
  height: 50px;
  border-radius: 10px;
  border: none;
  background-color: #1775ee;
  color: white;
  font-size: 20px;
  font-weight: 500;
  cursor: pointer;
}
.loginForgot{
  text-align: center;
  color: #1775ee;
}
.loginRegisterBtn{
  height: 50px;
  border-radius: 10px;
  border: none;
  background-color: #42b72a;
  color: white;
  font-size: 20px;
  font-weight: 500;
  cursor: pointer;
}
.loginTex{
  text-align: center;
  font-size: 15px;
}
.loginClick{
  color: #1775ee;
  cursor: pointer;
}